.wrap {
  text-align: left;
  position: relative;
  max-width: 600px;
  margin: 0 auto;
}

.controls {
  position: relative;
}

.sortingButtons {
  display: none;
}

@media screen and (min-width: 500px) {
  .controls {
    width: 600px;
  }
  .sortingButtons {
    display: inline-block;
    position: absolute;
    right: 0;
    top: 0;
  }
}

.sortingButton {
  display: block;
  width: auto;
  color: #999999;
  background: #ccc;
  padding: 10px;
  margin: 0 0 0 10px;
  cursor: pointer;
  display: inline-block;
}

.sortingButton:hover {
  color: black;
}

.sortingButtonActive {
  background: black;
  color: white;
}

.sortingButtonActive:hover {
  color: white;
}

/*----control-----*/
.controlButton {
  display: block;
  width: auto;
  color: #999999;
  background: #ccc;
  padding: 10px;
  margin: 0 0 0 10px;
  cursor: pointer;
  display: inline-block;
}

.controlButton:hover {
  color: black;
}

.controlButtonActive {
  background: black;
  color: white;
}

.controlButtonActive:hover {
  color: white;
}

/*----data-----*/
.aRecordRow {
  display: block;
  padding: 10px;
  background: white;
  margin-top: 1px;
  box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.12);
  min-height: 100px;
}

.positionCube {
  width: 20px;
  height: 20px;
  display: inline-block;
  margin: 4px 10px 0 10px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.date, .date:visited {
  display: inline-block;
  padding: 0 10px;
  vertical-align: top;
  margin-top: 2px;
  color: #505050;
}

.date:hover {
  color: #787878;
}

.category {
  display: inline-block;
  vertical-align: top;
  margin-top: 2px;
  padding: 0 10px;
}

.avatar, .avatar:visited {
  float: left;
  text-decoration: none;
  color: #353736;
  text-align: center;
  width: 90px;
  overflow: hidden;
}

.avatar:hover {
  text-decoration: underline;
}

.avatarName {
  font-size: 14px;
  width: 90px;
  line-height: 1.2;
}

.content {
  margin-top: 4px;
  margin-left: 18px;
  line-height: 1.6;
  padding-left: 80px;
}



