@charset "UTF-8";
.wrap {
  display: inline-block;
  text-align: left;
}

.title {
  text-align: center;
  white-space: pre;
  line-height: 1.4;
}

.avatarItem {
  position: relative;
  display: inline-block;
}

.avatarImg {
  width: 40px;
  height: 40px;
  margin: 4px;
  border-radius: 50%;
  border-width: 3px;
  border-style: solid;
  display: inline-block;
  cursor: pointer;
}

.avatarImg:hover, .avatarImgActive {
  box-shadow: 0 0 0 4px #b4b4b4;
}

.KMT {
  border-color: #000095;
}

.DPP {
  border-color: #009A00;
}

.PFP {
  border-color: #FE6407;
}

.TSU {
  border-color: #A25B09;
}

.NP, .NONE {
  border-color: gray;
}

.NPU {
  border-color: rgba(194, 15, 81, 0.9);
}

/* hover 上的時候顯示的文字 */
.activeCube {
  position: absolute;
  z-index: 10;
  display: block;
  top: -60px;
  left: -35px;
  width: 120px;
  min-height: 54px;
  line-height: 1.4;
  text-align: center;
  color: black;
  background: white;
  padding: 8px;
  font-size: 14px;
  border: 2px solid #505050;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.12);
  cursor: pointer;
}



