$cubeSize: 20px;
$ayeColor: #00FFB0;
$nayColor: #FF0055;
$unknownColor: #FFF800;
$themeColor: #006060;

.wrap {
	display: inline-block;
	position: relative;
}
.partyTitle, .partyTitle:visited {
    color: black;
    text-decoration: none;
    padding-bottom: 2px;
    margin-bottom: 4px;
    display: inline-block;
    border-bottom: 1px solid rgb(53,55,54);
}
.partyTitle:hover {
	color: $themeColor;
	border-bottom: 1px solid $themeColor;
}
.positionWrap {
	position: relative;
	float: left;
}
.positionCube, .positionCube:visited {
	width: $cubeSize;
	height: $cubeSize;
	float: left;
	position:relative;
	cursor: pointer;
	color: black;
	text-decoration: none;

}
.positionCube:hover, .positionCubeActive {
	box-shadow: 0 0 0 2px black;
	z-index: 10;
}
.aye {
	background: $ayeColor;
}
.nay {
	background: $nayColor;
}
.unknown {
	background: $unknownColor;
}
/* hover 上的時候顯示的文字 */
.activeBlock {
	position: absolute;
	top: -160px;
	z-index: 10;
}
.activeCube, .activeCube:visited {
	width: 100%;
	background: white;
	text-align: left;
	padding: 10px;
	font-size: 14px;
	line-height: 1.4;

	width: 340px;
	border: 1px solid;
  	
	box-shadow: 2px 2px 4px 0 rgba(0,0,0,0.12);

	cursor: pointer;
	display: block;
	color: black;
	text-decoration: none;

}
.activeContent {
	height: 100px;
	overflow: hidden;
	margin: 5px 0;
}
.caucus:after {
	content: '令';
	color: #FFFF1B;
	border: 1px solid #FFFF1B;
	position: absolute;
	top: 0;
	left: 0;
	width: calc(100% - 2px);
}





