@charset "UTF-8";
.wrap {
  display: inline-block;
}

.positionWrap {
  position: relative;
  float: left;
}

.positionCube, .positionCube:visited {
  width: 20px;
  height: 20px;
  float: left;
  position: relative;
  cursor: pointer;
  color: black;
  text-decoration: none;
}

.positionCube:hover, .positionCubeActive {
  box-shadow: 0 0 0 2px black;
  z-index: 10;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.KMT {
  background: #000095;
}

.DPP {
  background: #009A00;
}

.PFP {
  background: #FE6407;
}

.TSU {
  background: #A25B09;
}

.NP {
  border-color: gray;
}

.NPU {
  border-color: rgba(194, 15, 81, 0.9);
}

/* hover 上的時候顯示的文字 */
.activeBlock {
  position: absolute;
  top: -160px;
  z-index: 10;
}

.activeBlockClose {
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.activeCube, .activeCube:visited {
  width: 100%;
  background: white;
  text-align: left;
  padding: 10px;
  font-size: 14px;
  line-height: 1.4;
  width: 340px;
  border: 1px solid;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.12);
  cursor: pointer;
  display: block;
  color: black;
  text-decoration: none;
}

.activeContent {
  height: 100px;
  overflow: hidden;
  margin: 5px 0;
}

.caucus:after {
  content: '令';
  color: #FFFF1B;
  border: 1px solid #FFFF1B;
  position: absolute;
  top: 0;
  left: 0;
  width: calc(100% - 2px);
}



