@charset "UTF-8";
.wrap {
  display: inline-block;
  margin: 20px;
  vertical-align: top;
}

.title {
  margin: 5px;
  font-size: 20px;
}

/* 上方，下方 */
.countWrap {
  border: 2px solid black;
  display: inline-block;
}

.countTotal {
  display: inline-block;
  width: 60px;
  background: black;
  color: white;
}

.countItem {
  display: inline-block;
  width: 20px;
}

.cubesOuterWrap {
  margin: 20px;
}

/* 一個一個表態 */
.positionWrap {
  position: relative;
  float: left;
}

.positionCube {
  width: 20px;
  height: 20px;
  float: left;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

/* hover 上的時候顯示的文字 */
.activeCube {
  width: 100%;
  background: white;
  position: absolute;
  text-align: left;
  padding: 10px;
  font-size: 14px;
  line-height: 1.4;
  bottom: -100px;
  height: 90px;
  width: 340px;
  border: 1px solid;
  overflow: hidden;
  z-index: 1;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.12);
}



