.wrap {
  background: lightgray;
  padding: 20px;
  margin-top: 200px;
}

.innerWrap {
  margin: 0 auto;
  max-width: 1200px;
}

.title {
  font-size: 20px;
  margin: 20px 0;
}

.emailLabel {
  margin: 0 0 5px 2px;
  display: block;
}

.red {
  color: #D65A5A;
}

.showAlert {
  display: inline-block;
  color: #D65A5A;
}

.hideAlert {
  display: none;
}

.emailInput {
  display: inline-block;
  height: 40px;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
}

.emailInput:focus {
  outline-width: 0;
}

.submit {
  display: block;
  height: 40px;
  border: none;
  padding: 0 8px;
  color: white;
  background: #006060;
  margin-top: 10px;
}

@media all and (min-width: 450px) {
  .submit {
    display: inline-block;
    margin: 0 10px;
  }
}

.lawInfo {
  font-size: 14px;
  margin: 10px 0;
  line-height: 1.4;
}

.lawInfoTitle {
  color: #2E2828;
  margin-bottom: 10px;
}

.lawInfoContent {
  color: gray;
}

/* footer links */
.bottomInfo {
  border-top: 1px dashed #353736;
  margin-top: 20px;
  padding: 10px 0;
  line-height: 1.5;
}

.rightFooterLinks {
  width: 100%;
  margin-top: 50px;
}

@media all and (min-width: 800px) {
  .rightFooterLinks {
    text-align: right;
  }
}

.linkItem, .linkItem:visited {
  display: inline-block;
  color: black;
  text-decoration: none;
  padding: 10px;
}

.linkItem:hover {
  background: #b4b4b4;
  border-radius: 4px;
}



